@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.menu {
    position: absolute;
    top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    display: flex;
}

.menu a {
    text-decoration: none;
}

.social {
    margin-right: 14px;
    display: flex;
    align-items: center;
    transition: transform 0.7s;
}

.social:hover {
    transform: scale(1.1);
}

.social h3 {
    color: rgb(255, 255, 255);
    font-size: 24px;
    font-family: 'Roboto', sans-serif;
    margin-left: 8px;
    margin-right: 8px;
    user-select: none;
}

@media (max-width: 628px) {
    .social h3 {
        font-size: 18px;
        
    }
    @media (max-width: 400px) {
        .social h3 {
        display: none;
    }
}


}