@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

.app {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Roboto', sans-serif;
    background-color: #1B1B1B;
}

h1 {
    font-size: 50px;
    user-select: none;
    color: #FFA31A;
}

span {
    margin: 12px 0;
    font-size: 24px;
    user-select: none;
    color: #FFA31A;
}

.area-input {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: slideY 2s;
}

.area-input input {
    width: 100%;
    margin-bottom: 12px;
    height: 46px;
    background-color: #121212;
    border: 0;
    border-radius: 12px;
    color: whitesmoke;
    padding: 0 12px;
    text-align: center;
    font-size: 18px;
}

.area-input button {
    width: 100%;
    height: 46px;
    user-select: none;
    border: 0;
    border-radius: 12px;
    background-color: #cccccc;
    color: #000000;
    font-size: 32px;
    font-family: 'Roboto', sans-serif;
    transition: transform 0.7s;
}
@keyframes slideY {
    from {
    transform: translateY(90px);
    } to {
    transform: translateY(0px);
    }
}

.area-input button:hover {
background-color: #ada9a9;
transform: scale(1.05);
}

h2 {
    font-size: 28px;
    margin-top: 32px;
    color: #FFA31A;
}

@media (max-width: 628px) {
    h1 {
        font-size: 36px;
    }
    
    span {
        font-size: 20px;
    }
    
    .area-input {
    width: 300px;
    }

    .area-input input {
    width: 100%;
    height: 48px;
    font-size: 18px;
    }
    .area-input button {
        width: 70%;
        height: 42px;
        font-size: 28px;
    }

    h2 {
    font-size: 24px;
   }

}